@import "../../assets/style/app";

.header {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 150;
  padding: 28px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  background: rgba(7, 9, 19, 0);
  transition: 0.3s;

  @include desktop {
    padding: 34px 60px;
  }

  &_scroll {
    padding-top: 16px;
    padding-bottom: 16px;
    background: rgba(7, 9, 19, 1);

    @include desktop {
      padding-top: 20px;
      padding-bottom: 20px;
    }
  }

  &_hide {
    transform: translateY(-101%);
  }



  .logo {
    display: flex;
    align-items: center;

    img {
      display: block;
      width: 38px;
      height: 38px;
    }

    p {
      margin-left: 13px;
      @include Outfit;
      font-weight: 500;
      font-size: 23.5556px;
      line-height: 120%;
      color: #FFFFFF;
    }
  }

  .links {
    @include onlyDesktop;
    position: absolute;
    left: 50%;
    //top: 34px;
    top: 50%;
    //transform: translate(-50%, 0);
    transform: translate(-50%, -50%);
    padding: 10px 24px;
    border: 1px solid rgba(255,255,255,0.2);
    border-radius: 999px;

    .link {
      text-decoration: none;
      font-weight: 400;
      font-size: 14px;
      line-height: 150%;
      align-items: center;
      color: #FFFFFF;
      transition: 0.3s;

      &:not(:first-child) {
        margin-left: 24px;
      }

      &:hover {
        color: #D1D1DB;
      }
    }
  }

  .right {
    display: flex;
    align-items: center;

    .launchBtn {
      @include button_outlined;
      padding: 10px 16px;

      @include desktop {
        padding: 10px 24px;
      }
    }

    .burgerBtn {
      @include onlyMobile;
      @include centerRow;
      @include button;
      margin-left: 8px;
      width: 40px;
      height: 40px;
      border-radius: 50%;
      background: #FFF;
    }
  }
}
